.index-img{
  width: 100%;
  height: 240px;
}

.krepko{
  font-weight: bold;
}

.post{
  background-color: lightyellow;
}
.posts{
  background-color: lightgoldenrodyellow;
  border: 2px solid black;
}
.glava {
  background-color: #337ab7;
  font-weight: bold;
  color: white;
}
.info-box{
  background: lightcyan;
  border: 1px solid lightgray;
  border-radius: 5px;
  padding: 15px;
}

.table-header{
  color: #FFFFFF;
  background: #33CC33;
  font-weight: bold;
}
.comment-box{
  border: 1px solid lightgray;
  border-radius: 5px;
  padding: 15px;
  margin: 10px;
  background: #ccff99;
}

.default-box{
  padding: 15px;
  margin: 15px;
  border: 1px solid lightgray;
  border-radius: 5px;
}